@import "../../scss/variables";
@import "../../scss/mixins";

tr:has(.summary) {
  td { 
    background-color: $tableSummaryGray !important;
    font-weight: bold;
  }  
}

.opportunityTable {
  table {
    border-collapse: separate;
  }

  tr {
    th {
      background-color: rgb(236, 235, 235);
      z-index: 2;
      position: sticky;
      top: 0;
      vertical-align: middle;
      text-align: center;
    }
    td {
      background-color: $white;
      &:first-of-type {
        font-weight: 600;
      }
      & > div {
        display: flex;
        align-items: center;
        gap: 5px;
        * {
          max-width: 150px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
      }
      &:has(.expandedRow) {
        padding-left: 30px !important;
        div {
          width: 100%;
          span {
            width: 100%;
            max-width: none;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 2px;
            a {
              max-width: 85%;
              white-space: nowrap;
              text-overflow: ellipsis;
              overflow: hidden;
            }
            i {
              margin-left: auto;
            }
          }
        }
      }
    }
    .amt {
      font-weight: 600;
      margin-left: auto;
    }
    &:has(.expandedRow) {
      td {
        &:first-of-type {
          max-width: 200px;
          font-weight: 400;
        }
        .amt {
          font-weight: 400;
          margin-left: auto;
        }
      }
    }
    // &:nth-of-type(2) {
    //   td {
    //     background-color: #ffffe6 ;
    //   }
    // }

    .even {
      background-color: #facbcb;
      div {
        justify-content: flex-end;
        width: 100%;
      }
    }

    .odd {
      background-color: #75f2f2;
      div {
        justify-content: flex-end;
        width: 100%;
      }
    }

    .total {
      background-color: #f2f29f;
      div {
        justify-content: flex-end;
        width: 100%;
      }
    }

    .innerodd {
      background-color: #d8eaea;
      div {
        justify-content: flex-end;
        width: 100%;
      }
    }

    .innereven {
      background-color: #f5e5e5;
      div {
        justify-content: flex-end;
        width: 100%;
      }
    }

    .amount {
      justify-content: flex-end;
    }
  }
}

.opportunityExecTable {
  max-height: 40vh;
  overflow: auto;
  table {
    border-collapse: separate;
  }
  tr {
    &:first-of-type {
      // position: sticky;
      top: 0;
      z-index: 5;
    }
    &:has(.expandedRow) {
      td {
        &:first-child {
          span {
            width: 100%;
            text-align: center;
          }
        }
      }
    }
    th {
      width: fit-content;
      vertical-align: middle;
      white-space: nowrap;
      text-align: center;
    }
  }
  th,
  td {
    &:nth-of-type(1) {
      max-width: 200px;
      min-width: 200px;
      position: sticky;
      left: 0;
      z-index: 3;
    }
    &.customer {
      max-width: 180px;
      min-width: 180px;
      position: sticky;
      left: 200px;
      z-index: 3;
    }
    &.opportunity {
      max-width: 160px;
      min-width: 160px;
      position: sticky;
      left: 380px;
      z-index: 3;
    }
  }
  th {
    &:nth-of-type(1),
    &.customer,
    &.opportunity {
      z-index: 6;
    }
  }
  td {
    &:nth-of-type(1),
    &.customer,
    &.opportunity {
      background-color: $white;
    }
    &.opportunity {
      div {
        width: 100%;
        justify-content: space-between;
        a {
          max-width: calc(100% - 20px);
        }
      }
    }
  }
}
